@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
    background: #F4F4F4;
    overflow-x: hidden;
}

.header {
    position: relative;
    width: 100vw;
    height: 300px;
    background-color: royalblue;
}

.background-image {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(/TODO_App_React/static/media/bg.22df0311.jpg) center no-repeat;
    background-size: cover;
}

.background-image::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #7B583D;
    left: 0;
    opacity: 0.2;
}

.header > h1 {
    position: absolute;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    padding: 30px 20px;
}

.input-form {
    position: absolute;
    font-family: 'Ubuntu', sans-serif;
    display: -webkit-flex;
    display: flex;
    grid-gap: 50px;
    gap: 50px;
    bottom: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.input-form > input {
    border: none;
    border-radius: 5px;
    background-color: #E3E1E1;
    color: #6B6B6B;
    font-weight: 700;
    height: 25px;
}

.input-form > input::-webkit-input-placeholder {
    font-weight: 600;
}

.input-form > input:-ms-input-placeholder {
    font-weight: 600;
}

.input-form > input::placeholder {
    font-weight: 600;
}

.input-form > input:focus {
    outline: none;
}

.input-form > button {
    border: none;
    width: 50px;
    border-radius: 10px;
    background-color: #A47551;
    color: #FFFDFD;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    cursor: pointer;
    box-shadow: 1px 3px 6px 2px #2d2222;
}

.input-form > button:focus {
    outline: none;
}

.input-form > button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

