.task-container {
    position: absolute;
    padding-top: 20px;
    padding-bottom: 20px;
    top: 300px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 50px;
    gap: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.task {
    position: relative;
    width: 200px;
    height: 150px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 1px 8px 12px 2px #cccbcb;
}

