.task > .top-border {
    position: absolute;
    width:40%;
    height: 4px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 1px;
    border-radius: 20px;
    /* background-color: slateblue; */
}

.task > .bottom-border {
    position: absolute;
    width:40%;
    height: 4px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 1px;
    border-radius: 20px;
    /* background-color: slateblue; */
}

.task > .left-border {
    position: absolute;
    width:40%;
    height: 4px;
    left: -37px;
    top: 0;
    bottom: 0;
    margin: auto;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    border-radius: 20px;
    /* background-color: slateblue; */
}

.task > .right-border {
    position: absolute;
    width:40%;
    height: 4px;
    right: -37px;
    top: 0;
    bottom: 0;
    margin: auto;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    border-radius: 20px;
    /* background-color: slateblue; */
}

.task > .date {
    padding-top: 20px;
    font-size: 15px;
    font-weight: 500;
}

