.task > button {
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.2s ease-in;
    background-color: white;
}

.task > button:focus {
    outline: none;
}

.task > button:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    transition: all 0.2s ease-in;
    color: slategrey;
}

.task > .btnUpdate {
    margin-right: 5px;
}

.task > .btnDelete {
    margin-left: 5px;
}




@-webkit-keyframes fading {
    0% { 
        opacity: 0; 
    }
    100% { 
        opacity: 1; 
    }
}




@keyframes fading {
    0% { 
        opacity: 0; 
    }
    100% { 
        opacity: 1; 
    }
}
